import * as React from 'react';
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Dropdown, DropdownItemProps, FormField} from "semantic-ui-react";
import {Node} from "../../../../model/Graph";
import {WizardContext} from "../../../../service/cody-wizard/context/wizard-context";

interface OwnProps {
  command: Node | null;
  newAggregate: boolean | undefined;
  ctx: WizardContext;
  onNewAggregateChanged: (newAggregate: boolean) => void;
}

type NewAggregateSelectProps = OwnProps & WithNamespaces;

const NewAggregateSelect = (props: NewAggregateSelectProps) => {

  const options: DropdownItemProps[] = [
    {
      key: 'yes',
      text: props.t('app.form.yes'),
      value: true
    },
    {
      key: 'no',
      text: props.t('app.form.no'),
      value: false
    }
  ]

  return <FormField disabled={!props.command} inline={true}>
    <label>{props.t('insp.cody_wizard.step_command.new_aggregate_select_label') as string}</label>
    <Dropdown inline={true}
              disabled={!props.command}
              value={props.newAggregate}
              onChange={(e, data) => props.onNewAggregateChanged(data.value as boolean)}
              placeholder={props.t('insp.cody_wizard.step_command.new_aggregate_select_placeholder') as string}
              options={options} />
  </FormField>
};

export default withNamespaces()(NewAggregateSelect);
